import * as yup from 'yup';

export const sendValidationSchema = yup.object({
    message: yup.string().required(),
    countryCode: yup.number().typeError("countryCode must be a `number` type").positive().integer().required().min(100).max(999),
    delay: yup.number().typeError("delay must be a `number` type").positive().integer().required().min(2).max(60),
});